/** style.scss **/
.wp-block-create-block-tmy-team {
	overflow: hidden;
	position: relative;

	.team-gallery-inner-container {
		display: flex;

		&[data-direction="left"] {
			animation: marquee-left 5s linear infinite;
		}

		&[data-direction="right"] {
			animation: marquee-right 5s linear infinite;
		}

		img {
			object-fit: cover;
			height: 100%;
			min-width: 16vw;
			&:not(:last-child) {
				margin-right: 16px;
			}
		}
	}
}
